import React from 'react'
import {FooterContainer, FooterLink, FooterLinkItems, FooterLinksContainer, FooterLinksWrapper, FooterLinkTitle, FooterWrap, SocialMedia, SocialMediaWrap, SocialLogo, WebsiteRights, SocialIcons, SocialIconsLink} from './FooterElements'
import {FaFacebook, FaInstagram, FaTwitter, FaLinkedin} from 'react-icons/fa';
import {animateScroll as scroll} from 'react-scroll';

const Footer = () => {
    const toggleHome = () => {
        scroll.scrollToTop()
    }


    return (
       <FooterContainer>
           <FooterWrap>
               <FooterLinksContainer>
                   <FooterLinksWrapper>
                       <FooterLinkItems>
                           <FooterLinkTitle>About us</FooterLinkTitle>
                               <FooterLink to="/signin">How it works</FooterLink>
                               <FooterLink to="/signin">Testimonials</FooterLink>
                               <FooterLink to="/signin">Carreers</FooterLink>
                               <FooterLink to="/signin">Investors</FooterLink>
                               <FooterLink to="/signin">Terms of service</FooterLink>
                        </FooterLinkItems>
                        <FooterLinkItems>
                           <FooterLinkTitle>Contact us</FooterLinkTitle>
                               <FooterLink to="/signin">Contact</FooterLink>
                               <FooterLink to="/signin">Support</FooterLink>
                               <FooterLink to="/signin">Destinations</FooterLink>
                               <FooterLink to="/signin">Sponsership</FooterLink>
                        </FooterLinkItems>
                   </FooterLinksWrapper>
                   <FooterLinksWrapper>
                       <FooterLinkItems>
                           <FooterLinkTitle>Videos</FooterLinkTitle>
                               <FooterLink to="/signin">Submit Video</FooterLink>
                               <FooterLink to="/signin">Ambassadors</FooterLink>
                               <FooterLink to="/signin">Agency</FooterLink>
                               <FooterLink to="/signin">Influencer</FooterLink>
                        </FooterLinkItems>
                        <FooterLinkItems>
                           <FooterLinkTitle>Social Media</FooterLinkTitle>
                               <FooterLink to="/signin">Instagram</FooterLink>
                               <FooterLink to="/signin">Facebook</FooterLink>
                               <FooterLink to="/signin">Youtube</FooterLink>
                               <FooterLink to="/signin">Twitter</FooterLink>
                        </FooterLinkItems>
                   </FooterLinksWrapper>
               </FooterLinksContainer>
               <SocialMedia>
                   <SocialMediaWrap>
                       <SocialLogo to='/' onClick={toggleHome}>
                           adonix
                       </SocialLogo>
                       <WebsiteRights>adonix Â© {new Date().getFullYear()} All rights reserved.</WebsiteRights>
                       <SocialIcons>
                           <SocialIconsLink href="//www.facebook.com" target="_blank" aria-label="Facebook">
                                <FaFacebook />
                           </SocialIconsLink>
                           <SocialIconsLink href="//www.instagram.com" target="_blank" aria-label="Instagram">
                                <FaInstagram />
                           </SocialIconsLink>
                           <SocialIconsLink href="//www.twitter.com" target="_blank" aria-label="Twitter">
                                <FaTwitter />
                           </SocialIconsLink>
                           <SocialIconsLink href="//www.linkedin.com" target="_blank" aria-label="Linkedin">
                                <FaLinkedin />
                           </SocialIconsLink>
                       </SocialIcons>
                   </SocialMediaWrap>
               </SocialMedia>

           </FooterWrap>
       </FooterContainer>
    )
}

export default Footer
