import React, {useState} from 'react';
import Video from '../../videos/video.mp4';
import { HeroContainer, HeroBg, VideoBg, HeroContent, HeroH1, HeroBtnWrapper, HeroP, ArrowForward, ArrowRight } from './HeroElements';
import {Button} from '../ButtonElement';


const HeroSection = () => {
    const [hover, setHover] = useState(false)

    const onHover = () => {
        setHover(!hover)
    }

    return (
        <HeroContainer id="home">
              
              <HeroContent>
              <HeroBg></HeroBg>
                <HeroH1>Virtual Marketing Made Easy</HeroH1>
                <HeroP>Sign up today for an exclusive free SEO ranking for your website or business</HeroP>
                <HeroBtnWrapper>
                    <Button to="signup"
                    onMouseEnter={onHover}
                    onMouseLeave={onHover}
                    primary="true"
                    dark="false"
                    smooth={true}
                    duration={500}
                    spy={true}
                    exact='true'
                    offset={-80}>
                    Get started {hover ? <ArrowForward /> : <ArrowRight />} 
                     </Button>
                    
                </HeroBtnWrapper>
            </HeroContent>
            <VideoBg autoPlay loop muted src={Video} type="video/mp4" />
            
        </HeroContainer>
        )
}

export default HeroSection
