import React from 'react'
import Icon1 from '../../images/test.jpeg'
import Icon2 from '../../images/test.jpeg'
import Icon3 from '../../images/test.jpeg'
import {ServicesContainer, ServicesCard, ServicesWrapper, ServicesH1, ServicesH2, ServicesP, ServicesIcon} from './ServicesElements'

const Services = () => {
    return (
        <ServicesContainer id="services">
          <ServicesH1>Our services</ServicesH1>  
              <ServicesWrapper>
                  <ServicesCard>
                      <ServicesIcon src={Icon1}/>
                        <ServicesH2>Reduce Expences</ServicesH2>
                        <ServicesP>We have it all</ServicesP>
                  </ServicesCard>
                  <ServicesCard>
                      <ServicesIcon src={Icon2}/>
                        <ServicesH2>Reduce Expences</ServicesH2>
                        <ServicesP>We have it all</ServicesP>
                  </ServicesCard>
                  <ServicesCard>
                      <ServicesIcon src={Icon3}/>
                        <ServicesH2>Reduce Expences</ServicesH2>
                        <ServicesP>We have it all</ServicesP>
                  </ServicesCard>
              </ServicesWrapper>
          
        </ServicesContainer>
    )
}

export default Services
